import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Router from 'next/router';

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
  },
});

export default function CenteredTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  function handleChange(_event: React.ChangeEvent<{}>, newValue: number) {
    console.log("setting new tab: ",newValue)
    setValue(newValue);
  }

  useEffect(() => {
    Router.pathname == "/"
    ? setValue("/")
    
    : setValue({ HeaderButtons: HeaderState.notHome });
  // Change the state of the Header depending on URL
  Router.events.on("routeChangeStart", url => {
    url == "/"
      ? setValue({ HeaderButtons: HeaderState.home })
      : setValue({ HeaderButtons: HeaderState.notHome });
  });  }, []); // run an effect and clean it up only once (on mount and unmount)

  return (
    <Paper className={classes.root}>
      <Tabs
        value={value}
        onChange={handleChange}
        indicatorColor="primary"
        textColor="primary"
        centered
      >
        <Tab label="Home" value="/" />
        <Tab label="My projects" value="/projects" />
        <Tab label="Contact" value="/contact" />
      </Tabs>
    </Paper>
  );
}